import tools from '@/common/tools.js'
import axios from 'axios'
import uploadimg from '@/base/uploadimg/uploadimg'
import VueCoreImageUpload from 'vue-core-image-upload'

export default {
  name: 'applyreturn',
  data: function() {
    return {
      recId: '',
      allData: {},
      returnType: 1,
      huowuPopStatus: false,
      reasonPopStatus: false,
      reason: 0,
      chooseIndex: 'reset',
      reasonIndex: 'reset',
      is_receive: 3,
      huowuzhuangtai: '请选择',
      reasonzhuangtai: '请选择',
      tuihuoTxtArr: ['多拍/拍错/不喜欢/效果不好', '质量问题', '卖家发错货', '未按约定时间发货', '收到商品破损/少件/有污渍', '其他'],
      src: 'http://img1.vued.vanthink.cn/vued0a233185b6027244f9d43e653227439a.png',
      data: {},
      imgDataList: [],
      refund_money: '',
      describe: '',
      upimgArr: []
    }
  },
  activated() {
    console.log('我是从新进来的1212121')
    this.recId = this.$route.query.recId
    this.returnType = this.$route.query.returnType  // 1是仅退款， 2是退款退货
    console.log(this.recId, this.returnType)
    this._getTuihuoData()
  },
  deactivated() {
    // this.$destroy()
    console.log('我走了')
    this.allData = {}
    this.reason = 0
    this.is_receive = 3
    this.chooseIndex = 'reset'
    this.reasonIndex = 'reset'
    this.huowuzhuangtai = '请选择'
    this.reasonzhuangtai = '请选择'
  },
  watch: {
    refund_money: function(val) {
      if (!val) {
        this.refund_money = ''
        return false
      }
      this.refund_money = val.match(/\d/ig) ? val.match(/\d+\.?\d{0,2}/).join('') : ''
    }
  },
  methods: {
    sendReturn() {
      if (this.is_receive == 3 && this.returnType == 1) {
        tools.showError('请选择货物状态')
        return false
      }
      if (this.reason == 0) {
        tools.showError('请选择退款原因')
        return false
      }
      console.log(this.refund_money)
      if (this.refund_money == '' || this.refund_money < 0 || this.refund_money > this.allData.order_amount) {
        tools.showError('退款金额需要大于0且不能超过该件商品实际支付的金额')
        return false
      }
      let url = '/Mobile/Order/return_goods.html'
      axios.post(url, {
        rec_id: this.recId,
        is_receive: this.is_receive,
        type: this.returnType == 1 ? 0 : 1,
        goods_num: this.allData.goods_num,
        reason: this.reason,
        describe: this.describe,
        order_id: this.allData.order_id,
        refund_money: this.refund_money,
        goods_id: this.allData.goods_id,
        spec_key: this.allData.spec_key,
        return_imgs: this.upimgArr
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            tools.showError(res.data.msg)
            setTimeout(() => {
              // this.$router.go(-1)
              this.$router.push({path:'orderdetail', query: {orderId: this.allData.order_id}})
            }, 2000)
          } else {
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    getUploadImgArr(arr) {
      console.log('我是父元素打印出来的', arr)
      this.upimgArr = arr
    },
    delUploadImg(index) {
      this.imgDataList.splice(index, 1)
    },
    imageuploaded(res) {
      console.log(res)
      if (res.state == 'SUCCESS') {
        this.imgDataList.push(res.url)
      }
    },
    closedReasonPop() {
      if (this.reason == 0) {
        tools.showError('请选择退货原因')
        return false
      }
      this.reasonPopStatus = false
      this.reasonzhuangtai = this.tuihuoTxtArr[this.reason-1]
    },
    changeReasonIndex(index) {
      /* if (this.reasonIndex === index) {
        this.reasonIndex = 'reset'
      } else { */
        this.reasonIndex = index
        /* this.nowYhqId = this.yhqData[this.chooseIndex].id */
        this.reason = index
        console.log('原因', this.reason)
      // }
    },
    selectGoodReason() {
      console.log(this.is_receive)
      if (this.returnType == 2) { // 退款退货，没有选择收货那一栏
        this.tuihuoTxtArr = ['多拍/拍错/不喜欢/效果不好', '质量问题', '卖家发错货', '未按约定时间发货', '收到商品破损/少件/有污渍', '其他']
        this.reasonPopStatus = true
      } else if (this.returnType == 1) {
        if (this.is_receive == 0) {
          this.tuihuoTxtArr = ['多拍/拍错/不喜欢/不想要', '快递无跟踪记录', '未按约定时间发货', '快递一直未送到', '空包裹/少货', '其他']
          this.reasonPopStatus = true
        } else if (this.is_receive == 1) {
          this.tuihuoTxtArr = ['多拍/拍错/不喜欢/效果不好', '质量问题', '卖家发错货', '未按约定时间发货', '收到商品破损/少件/有污渍', '其他']
          this.reasonPopStatus = true
        } else if (this.is_receive == 3 && this.returnType == 1) {
          tools.showError('请先选择货物状态')
        }
      }
    },
    closedHuowuPop() {
      if (this.is_receive == 3) {
        tools.showError('请选择货物状态')
        return false
      }
      this.huowuzhuangtai = (this.is_receive == 0 ? '未收到货' : '已收到货')
      this.huowuPopStatus = false
      this.reasonzhuangtai = '请选择'
      this.reason = 0
    },
    changeYhqIndex(index) {
      /* if (this.chooseIndex === index) {
        this.chooseIndex = 'reset'
      } else { */
      this.chooseIndex = index
        /* this.nowYhqId = this.yhqData[this.chooseIndex].id */
      this.is_receive = index
      /* } */
      console.log(this.chooseIndex)
    },
    selectGoodStatus() {
      this.huowuPopStatus = true
    },
    _getTuihuoData() {
      let url = '/Mobile/Order/return_goods.html'
      axios.get(url, {
        params: {
          rec_id: this.recId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            this.allData = res.data.data
            this.refund_money = this.allData.order_amount
          } else {
            tools.showError(res.data.msg)
            setTimeout(() => {
              this.$router.go(-1)
            }, 2000)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  components: {
    uploadimg,
    'vue-core-image-upload': VueCoreImageUpload
  }
}



